import React from "react"
import { Link } from "gatsby"

import Product from "../components/Product"

export default function Products({ products, category }) {
  let pageClass = ""
  if (category.length > 1) {
    pageClass = category.toLowerCase().split(" ").join("-")
  } else {
    pageClass = category.toLowerCase()
  }
  return (
    <div className="container">
      <header className={`page-header ${pageClass.toLowerCase()}-page-header`}>
        <h1>{category}</h1>
        <div className="bread-crumb">
          <Link to="/">Home</Link>
          <span className="color-gray">&gt;</span>
          {category}
        </div>
      </header>
      <div className="grid">
        {products.map(product => (
          <Product
            key={product.id}
            category={product.category}
            name={product.name}
            altName={product.alt_name}
            origin={product.origin}
            price={product.price}
            unit={product.unit}
            thumb={product.thumb}
            slug={product.slug}
          />
        ))}
      </div>
      <div className={`banner-page-bottom ${pageClass}-page-banner`}>
        <h2 className="h2">
          We invite you to explore our range of {category} in person at our
          store.
        </h2>
        <p className="subtitle">
          We are at{" "}
          <span className="address">
            Auburn Arcade, 12/75-77 Auburn Rd, Auburn NSW 2144
          </span>
        </p>
      </div>
    </div>
  )
}
