export default function Saffron({ data }) {
  const spices = data.allDataJson.nodes
  return (
    <Layout>
      <Products products={spices} category="Spices" />
    </Layout>
  )
}

export const query = graphql`
  query Spices {
    allDataJson(filter: { category: { eq: "Spices" } }) {
      nodes {
        category
        id
        name
        alt_name
        origin
        price
        slug
        unit
        thumb {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`
