import { graphql, Link } from "gatsby"
import React from "react"
import { getImage, GatsbyImage, StaticImage } from "gatsby-plugin-image"
import * as styles from "../styles/product.module.css"

const product = {
  category: "saffron",
  name: "Saffron Loose",
  origin: "Afghanistan",
  price: 200,
  unit: "gram",
  thumb: "",
  image: "",
}

export default function Product(props) {
  const { category, name, altName, origin, price, unit, thumb, slug } = props
  const thumbImage = getImage(thumb)
  const priceList = price.split(",")
  const unitList = unit.split(",")

  return (
    <Link to={`/${category.toLowerCase()}/${slug}`} className={styles.link}>
      <div className={styles.product}>
        <div className={styles.thumbnail}>
          {/* <img src={thumb} /> */}
          <GatsbyImage image={thumbImage} alt={name} />
        </div>
        <div className={styles.productDetails}>
          <p className="color-gray">{category}</p>
          <div className={styles.names}>
            <h2>{name}</h2>
            {altName && <h2 lang="fa">{altName}</h2>}
          </div>
          {origin && (
            <p className={styles.origin}>
              <span className="color-gray">Origin: </span>
              {origin}
            </p>
          )}
          {priceList.length > 1 ? (
            <div className={styles.pricesContainer}>
              <div>
                {priceList.map((price, index) => (
                  <span key={index} className={styles.price}>
                    ${price.trim()}
                  </span>
                ))}
              </div>
              <div className="mt-small">
                {unitList.map((unit, index) => (
                  <span key={index} className="color-gray">
                    {unit.trim()}
                  </span>
                ))}
              </div>
            </div>
          ) : (
            price && (
              <p className={styles.priceContainer}>
                <span className={styles.price}>${priceList[0]}</span>{" "}
                <span className="color-gray ml-small">per {unitList[0]}</span>
              </p>
            )
          )}
        </div>
      </div>
    </Link>
  )
}

